/* 
   Nature.co - Original Design CSS (Sadk Kalnan Versiyon)
    */

:root {
  /* Original Theme Colors (from index.html) - DARK (default) */
  --purple-dark: #1c1f23;
  --purple: #2c3038;
  --purple-mid: #3a3f47;
  --aubergine: #141618;
  --bg: #141618;
  --bg2: #1c1f23;
  --surface: #252830;
  --surface2: #2f3340;
  --border: #3a3f47;
  --text: #c9cdd4;
  --text-hi: #ffffff;
  --muted: #7a8090;
  --green: #2ecc71;
  --blue: #5b9bd5;
  --red: #e05555;
  --yellow: #f0c040;
  --own-bg: #2a4a7a;
  --incoming-bg: #263045;
  --accent: #5b9bd5;
  --accent2: #2a4a7a;
  --accent-light: #6dbf67;
  --orange: #f97316;
  --pink: #ec4899;
  
  /* Layout */
  --rail-width: 68px;
  --sidebar-width: 260px;
  --header-height: 54px;
}

/* LIGHT THEME */
[data-theme="light"] {
  --bg: #f5f6f7;
  --bg2: #ffffff;
  --surface: #ffffff;
  --surface2: #f0f1f2;
  --border: #e1e2e4;
  --text: #2e3338;
  --text-hi: #000000;
  --muted: #6a7079;
  --own-bg: #5b9bd5;
  --incoming-bg: #e9ecef;
}

/* NATURE THEME (Green) */
[data-theme="nature"] {
  --bg: #0d1f0c;
  --bg2: #1a3318;
  --surface: #2e5c28;
  --surface2: #3a7a34;
  --border: #4a8f40;
  --text: #a5d6a7;
  --text-hi: #e8f5e9;
  --muted: #66a862;
  --accent: #4a8f40;
  --accent-light: #6dbf67;
  --own-bg: #2e5c28;
  --incoming-bg: #1e3d18;
}

/* BLUE THEME */
[data-theme="blue"] {
  --bg: #0a0f1a;
  --bg2: #141b2e;
  --surface: #1e2a45;
  --surface2: #2a3a5a;
  --border: #3a4a6a;
  --text: #b3c5e7;
  --text-hi: #e8f0ff;
  --muted: #6a8ab0;
  --accent: #5b9bd5;
  --accent2: #2a4a7a;
  --own-bg: #2a4a7a;
  --incoming-bg: #1e2a45;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Inter', sans-serif;
  background: var(--bg);
  color: var(--text);
  line-height: 1.5;
  overflow: hidden;
}

.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

/* 
   Loading Screen
    */

.loading-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(145deg, var(--purple-dark) 0%, var(--bg2) 50%, #0a1a0a 100%);
  gap: 16px;
}

.loading-spinner {
  width: 48px;
  height: 48px;
}

.spinner {
  width: 100%;
  height: 100%;
  border: 3px solid var(--border);
  border-top-color: var(--accent);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.loading-screen p {
  color: var(--muted);
  font-size: 0.85rem;
}

/* 
   Server Select Screen (Original Style)
    */

.server-select-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 24px;
  background: linear-gradient(145deg, var(--purple-dark) 0%, var(--bg2) 40%, #0a1a0a 70%, #0a0a1a 100%);
}

.server-select-content {
  width: 100%;
  max-width: 400px;
  position: relative;
  z-index: 1;
}

.server-select-header {
  text-align: center;
  margin-bottom: 32px;
}

.server-logo {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.server-logo svg {
  filter: drop-shadow(0 0 24px rgba(74, 143, 64, 0.3));
}

.server-title {
  font-size: 1.5rem;
  font-weight: 900;
  color: var(--text-hi);
  margin-bottom: 6px;
}

.server-subtitle {
  font-size: 0.85rem;
  color: var(--muted);
}

.server-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.server-card {
  display: flex;
  align-items: center;
  gap: 14px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 18px 20px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.server-card:hover {
  border-color: rgba(74, 143, 64, 0.5);
  background: var(--surface2);
  transform: translateY(-2px);
}

.server-card-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  flex-shrink: 0;
  border: 1px solid;
}

.server-card-info {
  flex: 1;
  min-width: 0;
}

.server-card-name {
  font-size: 1rem;
  font-weight: 900;
  color: var(--text-hi);
  margin-bottom: 2px;
}

.server-card-desc {
  font-size: 0.76rem;
  color: var(--muted);
}

.server-card-arrow {
  font-size: 1.2rem;
}

.server-note {
  margin-top: 24px;
  text-align: center;
  font-size: 0.72rem;
  color: var(--muted);
}

/* 
   Login Screen (Original Style)
    */

.login-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 24px;
  background: linear-gradient(145deg, var(--purple-dark) 0%, var(--bg2) 50%, #0a1a0a 100%);
}

.login-box {
  background: var(--bg2);
  border: 1px solid var(--border);
  border-radius: 20px;
  padding: 24px;
  width: 100%;
  max-width: 360px;
}

.back-btn {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 6px;
  padding: 4px 8px;
  font-size: 0.75rem;
  color: var(--muted);
  cursor: pointer;
  margin-bottom: 8px;
  transition: all 0.15s;
}

.back-btn:hover {
  color: var(--text-hi);
  border-color: var(--accent);
}

.login-logo {
  display: flex;
  justify-content: center;
  margin-bottom: 4px;
}

.login-title {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 900;
  color: var(--text-hi);
  margin-bottom: 18px;
}

.login-tabs {
  display: flex;
  background: var(--surface);
  border-radius: 10px;
  padding: 3px;
  margin-bottom: 18px;
  gap: 3px;
}

.login-tab {
  flex: 1;
  padding: 9px;
  border: none;
  border-radius: 8px;
  font-size: 0.82rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.15s;
  background: transparent;
  color: var(--muted);
  text-align: center;
}

.login-tab.active {
  background: var(--surface2);
  color: var(--text-hi);
}

.login-error {
  background: rgba(224, 85, 85, 0.1);
  border: 1px solid rgba(224, 85, 85, 0.3);
  border-radius: 8px;
  padding: 10px 12px;
  margin-bottom: 14px;
  font-size: 0.8rem;
  color: var(--red);
  text-align: center;
}

.login-field {
  margin-bottom: 14px;
}

.login-field label {
  display: block;
  font-size: 0.72rem;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 6px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.login-field label .optional {
  font-size: 0.7rem;
  color: var(--muted);
  font-weight: normal;
  text-transform: none;
}

.login-field input,
.login-field select {
  width: 100%;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 10px 12px;
  font-size: 0.88rem;
  color: var(--text-hi);
  outline: none;
  transition: border-color 0.15s;
  font-family: inherit;
}

.login-field input:focus,
.login-field select:focus {
  border-color: var(--accent);
}

.login-field input::placeholder {
  color: var(--muted);
}

.password-input-wrap {
  position: relative;
}

.password-toggle {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: var(--muted);
  cursor: pointer;
  padding: 4px;
}

.login-user-info {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 14px;
  padding: 10px;
  background: var(--surface);
  border-radius: 8px;
}

.login-user-avatar {
  width: 32px;
  height: 32px;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.72rem;
  font-weight: 900;
  color: #fff;
  flex-shrink: 0;
}

.login-user-name {
  font-size: 0.82rem;
  font-weight: 700;
  color: var(--text-hi);
}

.login-user-sub {
  font-size: 0.7rem;
  color: var(--muted);
}

.login-change-user {
  margin-left: auto;
  background: none;
  border: none;
  font-size: 0.8rem;
  color: var(--blue);
  cursor: pointer;
}

.login-btn {
  width: 100%;
  background: var(--accent);
  border: none;
  border-radius: 8px;
  padding: 11px;
  font-size: 0.88rem;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  transition: all 0.15s;
}

.login-btn:hover:not(:disabled) {
  background: var(--accent-light);
}

.login-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.login-checkboxes {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 14px;
  padding: 10px 2px;
}

.checkbox-label {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  cursor: pointer;
}

.checkbox-label input {
  width: 18px;
  height: 18px;
  min-width: 18px;
  margin-top: 2px;
  accent-color: var(--green);
}

.checkbox-label span {
  font-size: 0.78rem;
  color: var(--text);
  line-height: 1.5;
}

.checkbox-label a {
  color: var(--green);
  text-decoration: underline;
}

.reg-closed {
  text-align: center;
  padding: 24px 16px;
}

.reg-closed-icon {
  font-size: 42px;
  margin-bottom: 12px;
}

.reg-closed h3 {
  font-size: 16px;
  font-weight: 700;
  color: var(--text-hi);
  margin-bottom: 8px;
}

.reg-closed p {
  font-size: 13px;
  color: var(--muted);
}

/* 
   Main App Layout (Original Desktop Style)
    */

/* Desktop Shell - Main Container */
#desktopShell {
  display: flex;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(145deg, #0a0a1a 0%, #0a0a0a 40%, #0a1a0a 70%, #0a0a1a 100%);
  overflow: hidden;
}

/* Icon Rail (Left Navigation) */
#deskRail {
  width: var(--rail-width);
  height: 100vh;
  background: rgba(16, 16, 20, 0.8);
  backdrop-filter: blur(10px);
  border-right: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 12px 0;
  flex-shrink: 0;
}

.rail-logo {
  padding: 4px;
  cursor: pointer;
  margin-bottom: 8px;
  transition: transform 0.2s;
}

.rail-logo:hover {
  transform: scale(1.1);
}

.rail-sep {
  width: 32px;
  height: 1px;
  background: var(--border);
  margin: 8px 0;
}

.rail-btn {
  width: 44px;
  height: 44px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  cursor: pointer;
  margin: 2px 0;
  color: var(--muted);
  transition: all 0.15s;
  position: relative;
}

.rail-btn:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-hi);
  transform: translateX(2px);
}

.rail-btn.act {
  background: var(--surface2);
  color: var(--accent-light);
}

.rail-btn.act::before {
  content: '';
  position: absolute;
  left: -12px;
  width: 4px;
  height: 24px;
  background: var(--accent-light);
  border-radius: 0 4px 4px 0;
}

.rail-btn-ic {
  display: flex;
  align-items: center;
  justify-content: center;
}

#deskRailUser {
  flex: 1;
  display: flex;
  align-items: flex-end;
  padding-bottom: 8px;
}

.sdot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--green);
  box-shadow: 0 0 8px var(--green);
}

.notif-pill {
  position: absolute;
  top: 2px;
  right: 2px;
  min-width: 16px;
  height: 16px;
  background: var(--red);
  border-radius: 100px;
  font-size: 0.6rem;
  font-weight: 700;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 4px;
}

/* Mobile Header */
.mobile-header {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: var(--header-height);
  background: var(--bg2);
  border-bottom: 1px solid var(--border);
  padding: 0 12px;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
}

.menu-toggle {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: var(--text);
  cursor: pointer;
}

.mobile-title {
  font-size: 1rem;
  font-weight: 900;
  color: var(--text-hi);
}

.mobile-avatar {
  width: 36px;
  height: 36px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.7rem;
  font-weight: 900;
  color: #fff;
  cursor: pointer;
}

/* 
   Sidebar (Original Style with Glassmorphism)
    */

#deskSidebar, .desktop-sidebar {
  width: var(--sidebar-width);
  height: 100vh;
  background: linear-gradient(180deg, 
    rgba(24, 20, 42, 0.7) 0%, 
    rgba(16, 16, 20, 0.85) 30%,
    rgba(16, 16, 20, 0.9) 100%
  );
  backdrop-filter: blur(20px);
  border-right: 1px solid rgba(74, 143, 64, 0.1);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  box-shadow: 4px 0 24px rgba(0, 0, 0, 0.3);
}

#deskSidebarHeader, .sidebar-header {
  padding: 14px 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  flex-shrink: 0;
}

#deskSidebarTitle {
  font-size: 0.95rem;
  font-weight: 900;
  color: var(--text-hi);
  margin-bottom: 2px;
  display: flex;
  align-items: center;
  gap: 4px;
}

#deskSidebarSub {
  font-size: 0.68rem;
  color: var(--muted);
}

#deskBellBtn:hover {
  background: rgba(255, 255, 255, 0.05);
}

#deskSidebarAvatar:hover {
  border-color: var(--accent);
  transform: scale(1.05);
}

#deskSearch, .sidebar-search {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 10px 14px;
  padding: 10px 12px;
  background: rgba(24, 24, 30, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s;
}

#deskSearch:hover, .sidebar-search:hover {
  background: rgba(30, 30, 36, 0.7);
  border-color: rgba(74, 143, 64, 0.2);
}

/* Sidebar List */
#deskSideList, .sidebar-list {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 8px 10px;
}

#deskSideList::-webkit-scrollbar, .sidebar-list::-webkit-scrollbar {
  width: 6px;
}

#deskSideList::-webkit-scrollbar-track, .sidebar-list::-webkit-scrollbar-track {
  background: transparent;
}

#deskSideList::-webkit-scrollbar-thumb, .sidebar-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

#deskSideList::-webkit-scrollbar-thumb:hover, .sidebar-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.15);
}

/* Sidebar Section Headers */
.dsk-sec-hdr {
  display: flex;
  align-items: center;
  padding: 14px 10px 4px 12px;
  font-size: 0.7rem;
  font-weight: 900;
  color: rgba(255, 255, 255, 0.4);
  text-transform: uppercase;
  letter-spacing: 0.07em;
  cursor: pointer;
  user-select: none;
}

.dsk-sec-hdr .chev {
  font-size: 0.6rem;
  margin-right: 4px;
  transition: transform 0.15s;
}

.dsk-sec-hdr .sec-add-btn {
  margin-left: auto;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.4);
  transition: background 0.15s, color 0.15s;
  cursor: pointer;
}

.dsk-sec-hdr .sec-add-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

/* Sidebar Rows (Channels, DMs) */
.dsk-row {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 3px 10px 3px 12px;
  cursor: pointer;
  border-radius: 6px;
  margin: 0 6px;
  transition: background 0.1s;
  min-height: 32px;
  position: relative;
}

.dsk-row:hover {
  background: rgba(255, 255, 255, 0.07);
}

.dsk-row.act {
  background: rgba(255, 255, 255, 0.14);
}

.dsk-row.act .dsk-row-name,
.dsk-row:hover .dsk-row-name {
  color: rgba(255, 255, 255, 0.9);
}

.dsk-row-ic {
  width: 18px;
  text-align: center;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.45);
  flex-shrink: 0;
}

.dsk-row-av {
  width: 24px;
  height: 24px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.6rem;
  font-weight: 900;
  color: #fff;
  flex-shrink: 0;
  position: relative;
}

.dsk-row-av .r-dot {
  position: absolute;
  bottom: -2px;
  right: -2px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 2px solid #222529;
}

.dsk-row-av .r-dot.on {
  background: var(--green);
}

.dsk-row-av .r-dot.off {
  background: var(--muted);
}

.dsk-row-name {
  flex: 1;
  font-size: 0.87rem;
  color: rgba(255, 255, 255, 0.6);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.dsk-row-badge {
  background: var(--red);
  color: #fff;
  font-size: 0.58rem;
  font-weight: 900;
  border-radius: 100px;
  padding: 1px 5px;
  min-width: 16px;
  text-align: center;
}

.sidebar-list::-webkit-scrollbar-track {
  background: transparent;
}

.sidebar-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.sidebar-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.15);
}

.sidebar-section {
  margin-bottom: 16px;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 6px 8px 4px;
  cursor: default;
}

.section-header.clickable {
  cursor: pointer;
  border-radius: 4px;
  transition: background 0.15s;
}

.section-header.clickable:hover {
  background: rgba(255, 255, 255, 0.03);
}

.section-title {
  font-size: 0.65rem;
  font-weight: 800;
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: 0.06em;
  flex: 1;
}

.section-add-button {
  margin-left: auto;
  background: none;
  border: none;
  color: var(--muted);
  cursor: pointer;
  padding: 2px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s;
}

.section-add-button:hover {
  color: var(--text-hi);
  background: rgba(255, 255, 255, 0.05);
}

.section-items {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.section-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 10px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.15s;
  position: relative;
}

.section-item:hover {
  background: rgba(255, 255, 255, 0.04);
}

.section-item.active {
  background: rgba(74, 143, 64, 0.15);
}

.section-item.active::before {
  content: '';
  position: absolute;
  left: 0;
  width: 3px;
  height: 18px;
  background: var(--accent-light);
  border-radius: 0 4px 4px 0;
}

.item-icon {
  color: var(--muted);
  display: flex;
  align-items: center;
  font-size: 0.85rem;
  width: 18px;
  justify-content: center;
}

.channel-icon {
  font-size: 1rem;
}

.item-name {
  font-size: 0.88rem;
  color: var(--text);
  font-weight: 500;
  flex: 1;
}

.section-item.active .item-name {
  color: var(--text-hi);
  font-weight: 600;
}

.item-badge {
  background: rgba(74, 143, 64, 0.2);
  border: 1px solid rgba(74, 143, 64, 0.3);
  border-radius: 4px;
  padding: 2px 6px;
  font-size: 0.65rem;
  font-weight: 700;
  color: var(--accent-light);
}

/* DM Items */
.dm-item {
  gap: 8px;
}

.dm-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.7rem;
  font-weight: 900;
  color: #fff;
  flex-shrink: 0;
  position: relative;
}

.dm-avatar .online-indicator {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background: var(--green);
  border: 2px solid var(--bg2);
}

.sidebar-empty {
  text-align: center;
  padding: 32px 16px;
  color: var(--muted);
  font-size: 0.82rem;
}

.sidebar-empty .hint {
  font-size: 0.72rem;
  margin-top: 6px;
  opacity: 0.7;
}

/* 
   Main Content Area
    */

#deskMain, .main-content-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: var(--bg);
}

/* Empty State */
#deskEmptyState, .empty-state {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  text-align: center;
  gap: 16px;
}

.big-title {
  font-size: 1.5rem;
  font-weight: 900;
  color: var(--text-hi);
  margin-bottom: 8px;
}

.big-sub {
  font-size: 0.9rem;
  color: var(--muted);
  max-width: 400px;
  line-height: 1.5;
}

.empty-state-actions {
  display: flex;
  gap: 12px;
  margin-top: 16px;
  flex-wrap: wrap;
  justify-content: center;
}

.action-button {
  padding: 10px 18px;
  border: none;
  border-radius: 10px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  font-family: inherit;
}

.action-button.primary {
  background: rgba(74, 143, 64, 0.15);
  border: 1px solid rgba(74, 143, 64, 0.25);
  color: var(--accent-light);
}

.action-button.primary:hover {
  background: rgba(74, 143, 64, 0.25);
  border-color: rgba(74, 143, 64, 0.4);
  transform: translateY(-2px);
}

.action-button.secondary {
  background: rgba(91, 155, 213, 0.12);
  border: 1px solid rgba(91, 155, 213, 0.2);
  color: #90caf9;
}

.action-button.secondary:hover {
  background: rgba(91, 155, 213, 0.2);
  border-color: rgba(91, 155, 213, 0.35);
  transform: translateY(-2px);
}

.user-info {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
  cursor: pointer;
}

.user-avatar {
  width: 34px;
  height: 34px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 900;
  color: #fff;
}

.user-details {
  display: flex;
  flex-direction: column;
}

.username {
  font-size: 0.82rem;
  font-weight: 700;
  color: var(--text-hi);
}

.user-status {
  font-size: 0.68rem;
  color: var(--muted);
}

.logout-btn {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 8px;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.logout-btn:hover {
  background: rgba(224, 85, 85, 0.1);
  color: var(--red);
}

/* 
   Main Content Area
    */

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: var(--bg);
}

/* Welcome Screen */
.welcome-screen {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 24px;
  text-align: center;
}

.welcome-icon {
  position: relative;
  margin-bottom: 8px;
}

.welcome-screen h2 {
  font-size: 1.2rem;
  font-weight: 900;
  color: var(--text-hi);
  margin-bottom: 8px;
}

.welcome-screen p {
  font-size: 0.85rem;
  color: var(--muted);
  max-width: 400px;
}

.welcome-actions {
  margin-top: 16px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

.welcome-btn {
  background: rgba(74, 143, 64, 0.15);
  border: 1px solid rgba(74, 143, 64, 0.25);
  border-radius: 10px;
  padding: 8px 14px;
  font-size: 0.78rem;
  color: #a5d6a7;
  cursor: pointer;
  transition: background 0.15s;
}

.welcome-btn:hover {
  background: rgba(74, 143, 64, 0.25);
}

.welcome-btn.blue {
  background: rgba(91, 155, 213, 0.12);
  border-color: rgba(91, 155, 213, 0.2);
  color: #90caf9;
}

.welcome-btn.blue:hover {
  background: rgba(91, 155, 213, 0.2);
}

/* 
   Chat Area (Original Style)
    */

.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.chat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 16px;
  background: var(--bg2);
  border-bottom: 1px solid var(--border);
  flex-shrink: 0;
}

.chat-header-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.chat-icon {
  font-size: 1.1rem;
  color: var(--muted);
}

.chat-name {
  font-size: 0.95rem;
  font-weight: 800;
  color: var(--text-hi);
}

.chat-description {
  font-size: 0.72rem;
  color: var(--muted);
}

.chat-header-actions {
  display: flex;
  gap: 4px;
}

.header-btn {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 8px;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
  font-size: 1rem;
}

.header-btn:hover,
.header-btn.active {
  background: var(--surface);
  color: var(--text-hi);
}

/* Messages Container */
.chat-messages-container {
  flex: 1;
  display: flex;
  overflow: hidden;
  position: relative;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.no-messages {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--muted);
}

.no-messages-icon {
  font-size: 48px;
  margin-bottom: 12px;
  opacity: 0.5;
}

.no-messages h3 {
  font-size: 1rem;
  color: var(--text);
  margin-bottom: 4px;
}

/* Message (Original Style) */
.message {
  display: flex;
  gap: 10px;
  padding: 4px 8px;
  border-radius: 6px;
  margin-left: 46px;
  position: relative;
  transition: background 0.15s;
}

.message:hover {
  background: var(--surface);
}

.message.with-avatar {
  margin-left: 0;
  padding-top: 8px;
}

.message-avatar {
  width: 38px;
  height: 38px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 900;
  color: #fff;
  flex-shrink: 0;
  cursor: pointer;
}

.message-content {
  flex: 1;
  min-width: 0;
}

.message-header {
  display: flex;
  align-items: baseline;
  gap: 8px;
  margin-bottom: 2px;
}

.message-author {
  font-size: 0.85rem;
  font-weight: 700;
  cursor: pointer;
}

.message-author:hover {
  text-decoration: underline;
}

.message-time {
  font-size: 0.65rem;
  color: var(--muted);
}

.message-edited {
  font-size: 0.62rem;
  color: var(--muted);
  font-style: italic;
}

.message-text {
  font-size: 0.9rem;
  color: var(--text-hi);
  word-wrap: break-word;
  line-height: 1.5;
  white-space: pre-wrap;
}

.message-actions {
  display: none;
  position: absolute;
  top: -8px;
  right: 8px;
  background: var(--surface2);
  border: 1px solid var(--border);
  border-radius: 6px;
  padding: 2px;
  gap: 2px;
}

.message:hover .message-actions {
  display: flex;
}

.msg-action-btn {
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 4px;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.msg-action-btn:hover {
  background: var(--surface);
  color: var(--text-hi);
}

.msg-action-btn.delete:hover {
  color: var(--red);
}

/* Members Panel */
.members-panel {
  width: 220px;
  background: var(--bg2);
  border-left: 1px solid var(--border);
  overflow-y: auto;
  flex-shrink: 0;
}

.members-panel h4 {
  padding: 14px 14px 8px;
  font-size: 0.68rem;
  font-weight: 800;
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: 0.06em;
}

.members-list {
  padding: 0 8px 16px;
}

.member-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 7px 8px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.15s;
}

.member-item:hover {
  background: var(--surface);
}

.member-avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.65rem;
  font-weight: 900;
  color: #fff;
  position: relative;
}

.status-dot {
  position: absolute;
  bottom: -2px;
  right: -2px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid var(--bg2);
}

.status-dot.online {
  background: var(--green);
}

.status-dot.offline {
  background: var(--muted);
}

.member-info {
  display: flex;
  flex-direction: column;
}

.member-name {
  font-size: 0.82rem;
  font-weight: 600;
  color: var(--text);
}

.member-role {
  font-size: 0.68rem;
  color: var(--muted);
}

/* Chat Input */
.chat-input-area {
  padding: 10px 16px 14px;
  background: var(--bg2);
  border-top: 1px solid var(--border);
}

.editing-indicator {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px 10px;
  background: var(--surface);
  border-radius: 6px;
  margin-bottom: 8px;
  font-size: 0.78rem;
  color: var(--blue);
}

.editing-indicator button {
  background: none;
  border: none;
  color: var(--muted);
  cursor: pointer;
  font-size: 0.75rem;
}

.chat-input-form {
  display: flex;
  align-items: center;
  gap: 6px;
  background: var(--surface);
  border-radius: 10px;
  padding: 6px 10px;
}

.input-btn {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: var(--muted);
  cursor: pointer;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.15s;
}

.input-btn:hover {
  background: var(--surface2);
  color: var(--text-hi);
}

.chat-input {
  flex: 1;
  background: none;
  border: none;
  outline: none;
  font-size: 0.9rem;
  color: var(--text-hi);
  font-family: inherit;
  resize: none;
}

.chat-input::placeholder {
  color: var(--muted);
}

.send-btn {
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--accent);
  border: none;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  transition: all 0.15s;
}

.send-btn:hover:not(:disabled) {
  background: var(--accent-light);
}

.send-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Context Menu */
.context-overlay {
  position: fixed;
  inset: 0;
  z-index: 999;
}

.context-menu {
  position: fixed;
  background: var(--surface2);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 6px;
  min-width: 150px;
  z-index: 1000;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

.context-menu button {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 9px 11px;
  background: none;
  border: none;
  border-radius: 6px;
  font-size: 0.82rem;
  color: var(--text);
  cursor: pointer;
  transition: background 0.15s;
}

.context-menu button:hover {
  background: var(--surface);
}

.context-menu button.delete {
  color: var(--red);
}

/* 
   Admin Panel (Original Style)
    */

.admin-panel {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.admin-panel.unauthorized {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.unauthorized-icon {
  font-size: 56px;
  margin-bottom: 14px;
}

.admin-panel.unauthorized h2 {
  font-size: 1.2rem;
  color: var(--text-hi);
  margin-bottom: 6px;
}

.admin-panel.unauthorized p {
  color: var(--muted);
  font-size: 0.9rem;
}

.admin-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 20px;
}

.admin-header h2 {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.2rem;
  font-weight: 900;
  color: var(--text-hi);
}

.server-badge {
  background: var(--accent);
  color: #fff;
  padding: 4px 12px;
  border-radius: 100px;
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
}

.admin-feedback {
  background: rgba(46, 204, 113, 0.1);
  border: 1px solid rgba(46, 204, 113, 0.3);
  border-radius: 10px;
  padding: 10px 14px;
  margin-bottom: 18px;
  font-size: 0.85rem;
  color: var(--green);
  text-align: center;
}

/* Stats */
.admin-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  gap: 10px;
  margin-bottom: 20px;
}

.stat-card {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 14px;
}

.stat-card svg {
  color: var(--accent);
}

.stat-info {
  display: flex;
  flex-direction: column;
}

.stat-value {
  font-size: 1.3rem;
  font-weight: 900;
  color: var(--text-hi);
}

.stat-label {
  font-size: 0.7rem;
  color: var(--muted);
}

/* Tabs */
.admin-tabs {
  display: flex;
  gap: 4px;
  background: var(--surface);
  border-radius: 10px;
  padding: 4px;
  margin-bottom: 18px;
  flex-wrap: wrap;
}

.admin-tab {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 9px 14px;
  background: none;
  border: none;
  border-radius: 8px;
  font-size: 0.82rem;
  font-weight: 600;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.admin-tab:hover {
  color: var(--text);
}

.admin-tab.active {
  background: var(--surface2);
  color: var(--text-hi);
}

/* Section Header */
.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 14px;
}

.section-header h3 {
  font-size: 0.95rem;
  font-weight: 800;
  color: var(--text-hi);
}

.section-header .count {
  font-size: 0.75rem;
  color: var(--muted);
}

.create-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  background: var(--accent);
  border: none;
  border-radius: 8px;
  padding: 8px 14px;
  font-size: 0.8rem;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  transition: all 0.15s;
}

.create-btn:hover {
  background: var(--accent-light);
}

/* Users List */
.users-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.user-row {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 12px 14px;
}

.user-row.banned {
  opacity: 0.6;
  background: rgba(224, 85, 85, 0.05);
  border-color: rgba(224, 85, 85, 0.2);
}

.user-row .user-avatar {
  width: 38px;
  height: 38px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 900;
  color: #fff;
  position: relative;
}

.user-row .user-avatar .status {
  position: absolute;
  bottom: -2px;
  right: -2px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid var(--surface);
}

.user-row .user-avatar .status.online {
  background: var(--green);
}

.user-row .user-avatar .status.offline {
  background: var(--muted);
}

.user-row .user-info {
  flex: 1;
  min-width: 0;
}

.user-row .user-name {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-hi);
  flex-wrap: wrap;
}

.role-badge {
  font-size: 0.62rem;
  font-weight: 700;
  padding: 2px 8px;
  border-radius: 100px;
}

.role-badge.admin {
  background: rgba(249, 115, 22, 0.15);
  color: var(--orange);
}

.role-badge.mod {
  background: rgba(91, 155, 213, 0.15);
  color: var(--blue);
}

.role-badge.banned {
  background: rgba(224, 85, 85, 0.15);
  color: var(--red);
}

.user-row .user-email {
  font-size: 0.75rem;
  color: var(--muted);
}

.user-actions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.role-select {
  background: var(--surface2);
  border: 1px solid var(--border);
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 0.78rem;
  color: var(--text);
  cursor: pointer;
  outline: none;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 7px 12px;
  border: none;
  border-radius: 7px;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s;
}

.action-btn.ban {
  background: rgba(224, 85, 85, 0.1);
  color: var(--red);
}

.action-btn.ban:hover {
  background: rgba(224, 85, 85, 0.2);
}

.action-btn.unban {
  background: rgba(46, 204, 113, 0.1);
  color: var(--green);
}

.action-btn.unban:hover {
  background: rgba(46, 204, 113, 0.2);
}

/* Room Form */
.room-form {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 18px;
  margin-bottom: 14px;
}

.room-form h4 {
  font-size: 0.92rem;
  font-weight: 800;
  color: var(--text-hi);
  margin-bottom: 14px;
}

.form-group {
  margin-bottom: 12px;
}

.form-group label {
  display: block;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 6px;
}

.form-group input,
.form-group select {
  width: 100%;
  background: var(--surface2);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 9px 11px;
  font-size: 0.85rem;
  color: var(--text-hi);
  outline: none;
  font-family: inherit;
}

.form-group input:focus,
.form-group select:focus {
  border-color: var(--accent);
}

.form-group.checkbox label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.form-group.checkbox input {
  width: auto;
}

.form-actions {
  display: flex;
  gap: 8px;
  justify-content: flex-end;
}

.cancel-btn,
.submit-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 9px 14px;
  border: none;
  border-radius: 8px;
  font-size: 0.82rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s;
}

.cancel-btn {
  background: var(--surface2);
  color: var(--muted);
}

.submit-btn {
  background: var(--accent);
  color: #fff;
}

.submit-btn:hover {
  background: var(--accent-light);
}

/* Rooms List */
.rooms-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.room-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 12px 14px;
}

.room-row .room-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.room-row .room-icon {
  font-size: 1.1rem;
}

.room-row .room-name {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-hi);
}

.room-row .room-desc {
  display: block;
  font-size: 0.72rem;
  color: var(--muted);
}

.room-actions {
  display: flex;
  gap: 4px;
}

.edit-btn,
.delete-btn {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 6px;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.edit-btn:hover {
  background: var(--surface2);
  color: var(--blue);
}

.delete-btn:hover {
  background: rgba(224, 85, 85, 0.1);
  color: var(--red);
}

.room-edit-form {
  display: flex;
  gap: 8px;
  width: 100%;
}

.room-edit-form input {
  flex: 1;
  background: var(--surface2);
  border: 1px solid var(--border);
  border-radius: 6px;
  padding: 8px 10px;
  font-size: 0.82rem;
  color: var(--text-hi);
  outline: none;
}

.room-edit-form button {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--surface2);
  border: none;
  border-radius: 6px;
  color: var(--text);
  cursor: pointer;
}

/* Settings */
.settings-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.setting-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 14px 16px;
}

.setting-info {
  display: flex;
  flex-direction: column;
}

.setting-name {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-hi);
}

.setting-desc {
  font-size: 0.75rem;
  color: var(--muted);
}

.toggle-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  transition: color 0.15s;
}

.toggle-btn.on {
  color: var(--green);
}

.toggle-btn.off {
  color: var(--muted);
}

/* Invites */
.invite-generator {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 22px;
  text-align: center;
}

.invite-generator p {
  font-size: 0.85rem;
  color: var(--text);
  margin-bottom: 14px;
}

.generate-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: var(--accent);
  border: none;
  border-radius: 10px;
  padding: 11px 22px;
  font-size: 0.88rem;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  transition: all 0.15s;
}

.generate-btn:hover {
  background: var(--accent-light);
}

.generated-code {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-top: 18px;
  padding: 14px;
  background: var(--surface2);
  border-radius: 10px;
}

.generated-code .code {
  font-size: 1.3rem;
  font-weight: 900;
  font-family: monospace;
  color: var(--accent);
  letter-spacing: 0.1em;
}

.generated-code .copy-btn {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 6px;
  padding: 7px 12px;
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--text);
  cursor: pointer;
  transition: all 0.15s;
}

.generated-code .copy-btn:hover {
  background: var(--accent);
  color: #fff;
  border-color: var(--accent);
}

/* 
   Forum Panel
    */

.forum-panel {
  flex: 1;
  overflow-y: auto;
}

.forum-header {
  padding: 14px 18px;
  background: var(--bg2);
  border-bottom: 1px solid var(--border);
}

.forum-header h2 {
  font-size: 1.1rem;
  font-weight: 900;
  color: var(--text-hi);
}

/* New Post */
.forum-new-post {
  display: flex;
  gap: 10px;
  padding: 12px 14px;
  background: var(--bg2);
  border-bottom: 1px solid var(--border);
  flex-shrink: 0;
  align-items: flex-start;
}

.post-avatar {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 900;
  color: #fff;
  flex-shrink: 0;
  cursor: pointer;
}

.post-form {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.post-form textarea {
  width: 100%;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 10px 12px;
  font-size: 0.88rem;
  color: var(--text-hi);
  outline: none;
  resize: none;
  font-family: inherit;
  transition: border-color 0.15s;
}

.post-form textarea:focus {
  border-color: var(--accent);
}

.post-form-actions {
  display: flex;
  align-items: center;
  gap: 8px;
  justify-content: flex-end;
}

.attach-btn {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 8px;
  color: var(--muted);
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.15s;
}

.attach-btn:hover {
  background: var(--surface);
  color: var(--text-hi);
}

.post-form .submit-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 9px 14px;
}

/* Forum Posts */
.forum-posts {
  padding: 14px;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.no-posts {
  text-align: center;
  padding: 40px 24px;
  color: var(--muted);
}

.no-posts-icon {
  font-size: 42px;
  margin-bottom: 10px;
  opacity: 0.5;
}

.no-posts h3 {
  font-size: 1rem;
  color: var(--text);
  margin-bottom: 4px;
}

/* Forum Post */
.forum-post {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 14px;
}

.post-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.post-meta {
  flex: 1;
}

.post-author {
  font-size: 0.88rem;
  font-weight: 700;
  cursor: pointer;
}

.post-author:hover {
  text-decoration: underline;
}

.post-time {
  display: block;
  font-size: 0.7rem;
  color: var(--muted);
}

.post-menu-btn {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 6px;
  color: var(--muted);
  cursor: pointer;
}

.post-content {
  font-size: 0.9rem;
  color: var(--text-hi);
  line-height: 1.6;
  margin-bottom: 10px;
  white-space: pre-wrap;
}

.post-image img {
  max-width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
}

.post-actions {
  display: flex;
  gap: 10px;
  padding-top: 10px;
  border-top: 1px solid var(--border);
}

.post-actions .action-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  background: none;
  border: none;
  padding: 7px 12px;
  border-radius: 8px;
  font-size: 0.82rem;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.post-actions .action-btn:hover {
  background: var(--surface2);
  color: var(--text);
}

.post-actions .action-btn.like.liked {
  color: var(--pink);
}

/* Comments */
.post-comments {
  margin-top: 14px;
  padding-top: 14px;
  border-top: 1px solid var(--border);
}

.comment {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.comment-avatar {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.6rem;
  font-weight: 900;
  color: #fff;
  flex-shrink: 0;
  cursor: pointer;
}

.comment-content {
  flex: 1;
  background: var(--surface2);
  border-radius: 10px;
  padding: 9px 11px;
}

.comment-author {
  font-size: 0.8rem;
  font-weight: 700;
  margin-right: 8px;
}

.comment-text {
  font-size: 0.82rem;
  color: var(--text);
}

.comment-time {
  display: block;
  font-size: 0.65rem;
  color: var(--muted);
  margin-top: 4px;
}

.comment-form {
  display: flex;
  gap: 8px;
  margin-top: 10px;
}

.comment-form input {
  flex: 1;
  background: var(--surface2);
  border: 1px solid var(--border);
  border-radius: 100px;
  padding: 9px 14px;
  font-size: 0.82rem;
  color: var(--text-hi);
  outline: none;
  font-family: inherit;
}

.comment-form button {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--accent);
  border: none;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
}

.comment-form button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* 
   Friends Panel
    */

.friends-panel {
  flex: 1;
  overflow-y: auto;
}

.friends-header {
  padding: 14px 18px;
  background: var(--bg2);
  border-bottom: 1px solid var(--border);
}

.friends-header h2 {
  font-size: 1.1rem;
  font-weight: 900;
  color: var(--text-hi);
}

.friends-tabs {
  display: flex;
  gap: 4px;
  padding: 10px 14px;
  background: var(--bg2);
  border-bottom: 1px solid var(--border);
}

.friends-tab {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 9px 14px;
  background: none;
  border: none;
  border-radius: 8px;
  font-size: 0.82rem;
  font-weight: 600;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.friends-tab:hover {
  background: var(--surface);
  color: var(--text);
}

.friends-tab.active {
  background: var(--surface2);
  color: var(--text-hi);
}

.friends-tab .count {
  background: var(--surface);
  padding: 2px 8px;
  border-radius: 100px;
  font-size: 0.7rem;
}

.friends-tab .count.highlight {
  background: var(--red);
  color: #fff;
}

.friends-content {
  padding: 14px;
}

/* Friends List */
.friends-list,
.requests-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.no-friends,
.no-requests {
  text-align: center;
  padding: 40px 24px;
  color: var(--muted);
}

.no-friends-icon,
.no-requests-icon {
  font-size: 42px;
  margin-bottom: 10px;
  opacity: 0.5;
}

.friend-item,
.request-item {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 12px 14px;
}

.friend-avatar,
.request-avatar {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.85rem;
  font-weight: 900;
  color: #fff;
  position: relative;
  cursor: pointer;
}

.friend-avatar .status,
.request-avatar .status {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 3px solid var(--surface);
}

.friend-avatar .status.online {
  background: var(--green);
}

.friend-avatar .status.offline {
  background: var(--muted);
}

.friend-info,
.request-info {
  flex: 1;
  cursor: pointer;
}

.friend-name,
.request-name {
  font-size: 0.92rem;
  font-weight: 700;
  color: var(--text-hi);
}

.friend-status,
.request-text {
  font-size: 0.75rem;
  color: var(--muted);
}

.message-btn {
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--surface2);
  border: none;
  border-radius: 10px;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
}

.message-btn:hover {
  background: var(--blue);
  color: #fff;
}

.request-actions {
  display: flex;
  gap: 6px;
}

.accept-btn,
.reject-btn {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.15s;
}

.accept-btn {
  background: rgba(46, 204, 113, 0.15);
  color: var(--green);
}

.accept-btn:hover {
  background: var(--green);
  color: #fff;
}

.reject-btn {
  background: rgba(224, 85, 85, 0.15);
  color: var(--red);
}

.reject-btn:hover {
  background: var(--red);
  color: #fff;
}

/* Add Friend */
.add-friend {
  max-width: 480px;
}

.search-box {
  display: flex;
  align-items: center;
  gap: 10px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 11px 14px;
  margin-bottom: 14px;
}

.search-box svg {
  color: var(--muted);
}

.search-box input {
  flex: 1;
  background: none;
  border: none;
  outline: none;
  font-size: 0.88rem;
  color: var(--text-hi);
  font-family: inherit;
}

.search-hint,
.no-results {
  text-align: center;
  padding: 24px;
  color: var(--muted);
}

.search-results {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.search-result-item {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 12px 14px;
}

.result-avatar {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 900;
  color: #fff;
}

.result-info {
  flex: 1;
}

.result-name {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-hi);
}

.add-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  background: var(--accent);
  border: none;
  border-radius: 8px;
  padding: 8px 14px;
  font-size: 0.8rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: all 0.15s;
}

.add-btn:hover {
  background: var(--accent-light);
}

/* 
   Profile Modal
    */

.profile-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 24px;
}

.profile-modal {
  background: var(--bg2);
  border: 1px solid var(--border);
  border-radius: 18px;
  width: 100%;
  max-width: 380px;
  overflow: hidden;
}

.profile-banner {
  height: 90px;
  position: relative;
}

.profile-banner .close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
  transition: background 0.15s;
}

.profile-banner .close-btn:hover {
  background: rgba(0, 0, 0, 0.7);
}

.profile-avatar-wrap {
  display: flex;
  justify-content: center;
  margin-top: -36px;
}

.profile-avatar {
  width: 72px;
  height: 72px;
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  font-weight: 900;
  color: #fff;
  border: 4px solid var(--bg2);
  position: relative;
}

.status-indicator {
  position: absolute;
  bottom: -4px;
  right: -4px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 4px solid var(--bg2);
}

.status-indicator.online {
  background: var(--green);
}

.status-indicator.offline {
  background: var(--muted);
}

.profile-info {
  text-align: center;
  padding: 14px 22px;
}

.profile-username {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 1.2rem;
  font-weight: 900;
  color: var(--text-hi);
  flex-wrap: wrap;
}

.profile-status {
  font-size: 0.82rem;
  color: var(--muted);
  margin-top: 4px;
}

.profile-details {
  padding: 0 22px 14px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.detail-item {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 0.82rem;
  color: var(--text);
}

.detail-item svg {
  color: var(--muted);
}

.profile-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 14px 22px;
  border-top: 1px solid var(--border);
}

.profile-actions .action-btn {
  flex: 1;
  min-width: 110px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 11px;
  border: none;
  border-radius: 10px;
  font-size: 0.82rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s;
}

.profile-actions .action-btn.message {
  background: var(--blue);
  color: #fff;
}

.profile-actions .action-btn.add-friend {
  background: var(--accent);
  color: #fff;
}

.profile-actions .action-btn.ban {
  background: rgba(224, 85, 85, 0.15);
  color: var(--red);
}

.profile-loading {
  padding: 44px;
  text-align: center;
  color: var(--muted);
}

/* 
   Responsive Design
    */

@media (max-width: 900px) {
  .icon-rail {
    display: none;
  }
  
  .sidebar-nav {
    display: block;
  }
  
  .members-panel {
    display: none;
  }
}

@media (max-width: 768px) {
  .mobile-header {
    display: flex;
  }

  .main-app {
    flex-direction: column;
    padding-top: var(--header-height);
  }

  .sidebar-overlay {
    display: block;
  }

  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    z-index: 200;
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .admin-stats {
    grid-template-columns: repeat(2, 1fr);
  }

  .admin-tabs {
    overflow-x: auto;
    flex-wrap: nowrap;
  }

  .user-row {
    flex-wrap: wrap;
  }

  .user-actions {
    width: 100%;
    margin-top: 8px;
    justify-content: flex-end;
  }

  .profile-actions {
    flex-direction: column;
  }

  .profile-actions .action-btn {
    min-width: 100%;
  }
}

@media (max-width: 480px) {
  .server-select-screen,
  .login-screen {
    padding: 16px;
  }

  .login-box {
    padding: 18px;
  }

  .admin-panel {
    padding: 14px;
  }

  .admin-stats {
    grid-template-columns: 1fr;
  }
}

/* 
   Scrollbar Styling
    */

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: var(--bg);
}

::-webkit-scrollbar-thumb {
  background: var(--border);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--muted);
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--border) var(--bg);
}

